
<template>
  <div>
    <Header />
       <div class="text-center bg-img-header py-5 header-top">
      <h2 class="font-weight-bold pt-4">Setting</h2>
      <div class="mx-auto border-lines mb-4"></div>
    </div>
     <div class="requests-bg-light-grew pt-3 pb-5">
        <div class="container pb-5">
            <div class="container box-shadow border-rounde mt-5 p-0 bg-white" style="width: 750px">
                <div class="">
                    <div class="px-4 pt-4 font-weight-bold">
                         <router-link class="text-dark font-weight-bold" to="/Profilesetting">
                        <div class="float-left">
                            <img src="../assets/img/ic_back_arrow.png" width="25" class="mr-3"/>Back
                        </div>
                         </router-link>
                    </div>
                    <div class="text-center h5 text-center font-weight-bold border-bottom pr-100 mb-0 pb-4">Help Center</div>
                 <table style="width:100%" class="box-shadow rouded">
                <tr class="px-4 border-bottom" v-for="(item, index) in help_response" :key="index">
                    <td>
                        <h3 class="p-2 text-green font-weight-bold">{{item.question}}</h3>
                        <div class="p-3">
                            <div>
                           {{item.answer}}
                            </div>
                        </div>
                    </td>
                </tr>
            </table>

                </div>
               <!-- <button class="mx-auto w-100 border-0 p-3 mt-3 bg-success text-center btn-green text-white border-r-bx text-uppercase">Submit</button> -->
            </div>
        </div>
    </div>
    <Footer />
  </div>
</template>
  
<script>
import Header from "./Header.vue";
import Footer from "./Footer.vue";
import apiServices from "../components/Service/apiservice";

export default {
  name: "Helpcenter",
  data() {
    return {
        help_response:[],
    };
  },
   mounted() {
     this.Help();
   },
  components: {
    Header,
    Footer,
  },
   methods: {
      async  Help(){
         var param = 
        "lang=" + "0";
        let res = await apiServices.makeAPICall(apiServices.postMethod,apiServices.URL + apiServices.help_center_listAPI,param,() => {});
        if(res.data.flag==1){
          this.help_response=res.data.data
          console.log(res.data.data,"res.data.data"); 
          }
        else
          {
            this.ToastAlert('error',res.data.msg)
          }
      },
       ToastAlert(id,msg) {
      const Toast = this.$swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: false,
        // didOpen: (toast) => {
        //   toast.addEventListener('mouseenter', this.$swal.stopTimer)
        //   toast.addEventListener('mouseleave', this.$swal.resumeTimer)
        // }
      })

      Toast.fire({
        icon: id,
        title: msg
      })
    },
  }
};
</script>
