<script>
import Header from "./Header.vue";
import Footer from "./Footer.vue";
import apiServices from "../components/Service/apiservice";

export default {
  name: "Teamsandconditions",
  data() {
    return {
       about_response:{},
    };
  },
   mounted() {
      this.Aboutus();
   },
